/* global */
body {
  font-family: 'Tajawal', sans-serif;
}
ul,
li,
a {
  all: unset;
}
a {
  cursor: pointer;
}
/*sidenav*/
.sideNav {
    width: 20%;
    position: fixed;
    float: left;
    background-color: #FFF;
    height: 100vh;
}
.sideNav .container {
  display: table;
  margin: 0 auto;
  position: relative;
  top: 6%;
} 
.sideNav .navlist-bg ul {
  display: flex;
  flex-direction: column;
}
.sideNav .logo a {
  top: 30px;
  background-color: #b9262c;
  padding: 10px;
  color: #FFF;
  font-weight: bold;
}
a.active {
  color: #b9262c;
}
.sideNav .logo .after-logo {
  top: -32px;
  left: 44px;
}
.navlist-xs {
  display: none;
}
.sideNav .navlist-xs li{
  top: 30px;
}
.sideNav .navlist-xs li i{
  font-size: 18px;
}
.sideNav .navlist-xs li a {
  font-size: 10px;
  position: relative;
  top: -2px;
}
