/*3*/
.tracking-in-expand {
	-webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
 @-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}







/*Media query*/
@media (max-width: 992px) {
  .navlist-bg {
    display: none;
  }
  .navlist-xs {
    display: block;
  }
  .sideNav .logo .after-logo {
    display: none;
  }
  .sideNav .container {
    position: relative;
    margin: 0 auto;
    top: 30px;
    left: 0px;
}
}
/* My Framwork */
.sections .icons li,
.sideNav {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.sideNav .navlist-bg li:hover a,
.sideNav .navlist-xs li:hover a {
  box-shadow: rgba(27, 31, 35, 0.04) 0px 1px 0px, rgba(255, 255, 255, 0.25) 0px 1px 0px inset;
  color: #b9262c;
}
.sideNav .navlist-b li a,
.sideNav .navlist-xs li a,
.sideNav .navlist-bg li,
.sideNav .navlist-xs li {
  padding: 10px 0;
}
.sideNav .navlist-bg li,
.sideNav .navlist-xs li {
  display: block;
}
.sideNav .logo .after-logo,
.sideNav .navlist-xs li {
  position: relative;
}
h1.decore {
  opacity: 0.1;
}
.section h1 i {
  color: #b9262c;
  margin-right: 10px;
}
